import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { EcosistemasPage } from './ecosistemas.page';

const routes: Routes = [
  {
    path: '',
    component: EcosistemasPage
  },
  {
    path: 'ws-ecos',
    loadChildren: () => import('../../Pages/ecosistemas/ws-ecos/ws-ecos.module').then( m => m.WsEcosPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class EcosistemasPageRoutingModule {}
